
body {
  margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

.header {
  width: 100%;
  height: 200px;
  background-color: #636d83;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  color: white;
}
.header .title {
  margin-left: 40px;
  margin-top: 35px;
}
.header .img {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}
.header .h3 {
  background-color: rgba(255, 255, 255, 0.3);
  padding: 0 10px;
}
.main {
  margin: 20px 40px;
}
.main .title {
  -webkit-text-decoration: #22aeb4;
          text-decoration: #22aeb4;
}
.main .title::before {
  content: '';
  width: 20px;
  height: 100%;
  background-color: #22aeb4;
}
.main .statistics {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  text-align: center;
  background-color: #f5f6f7;
  padding: 10px 40px;
  border-radius: 10px;
}
.main .statistics .number {
  color: #22aeb4;
  font-size: 20px;
  font-weight: 900;
}

.echarts {
  margin-top: 50px;
}
